package tools.mo3ta.reviwers.viewmodel

import com.russhwolf.settings.Settings
import dev.icerock.moko.mvvm.viewmodel.ViewModel
import io.ktor.client.HttpClient
import io.ktor.client.call.body
import io.ktor.client.plugins.contentnegotiation.ContentNegotiation
import io.ktor.client.request.get
import io.ktor.client.request.headers
import io.ktor.client.request.parameter
import io.ktor.http.HttpHeaders
import io.ktor.serialization.kotlinx.json.json
import kotlinx.coroutines.async
import kotlinx.coroutines.awaitAll
import kotlinx.coroutines.flow.MutableStateFlow
import kotlinx.coroutines.flow.asStateFlow
import kotlinx.coroutines.flow.update
import kotlinx.coroutines.launch
import kotlinx.serialization.json.Json
import tools.mo3ta.reviwers.model.PullRequest
import tools.mo3ta.reviwers.model.Review
import tools.mo3ta.reviwers.model.User
import tools.mo3ta.reviwers.screens.Keys


data class UserContribution(
    val name: String,
    val approved: Int,
    val commented: Int,
    val receivedComments: Int,
    val created: Int
)

data class PullsUiState(
    val currentPage: Int = 1,
    val pulls: Int = 0,
    val data: List<UserContribution> = emptyList(),
    val isLoading:Boolean = false,
    val isNextShown:Boolean = true,
    val sortTypes: SortTypes = SortTypes.APPROVALS,
)

enum class SortTypes {
    APPROVALS, COMMENTS, REVIEWED
}

class PullsViewModel : ViewModel() {

    private val settings = Settings()

    private val githubKey = settings.getString(Keys.API_KEY, "")
    private val ownerWithRepo  = settings.getString(Keys.OWNER_REPO, "")
    private val isEnterprise  = settings.getBoolean(Keys.IS_ENTERPRISE, false)
    private val enterprise  = settings.getString(Keys.ENTERPRISE, "")

    var currentPage = 1
    private var totalReviews:MutableList<Review> = mutableListOf()

    private val _uiState = MutableStateFlow(PullsUiState())
    val uiState = _uiState.asStateFlow()

    private val httpClient = HttpClient {
        install(ContentNegotiation) {
            json(json = Json {
                ignoreUnknownKeys = true
            })
            }
        }

    init {
         getPulls()
    }

    private fun prepareurl(isEnterprise: Boolean, enterprise: String): String {
        return if(isEnterprise){
            "github.${enterprise}.com/api/v3"
        }else{
            "api.github.com"
        }
    }

    private suspend  fun loadPullRequests(page:Int = 1): List<PullRequest> {
        val urlPrefix = prepareurl(isEnterprise, enterprise)
        return httpClient
            .get("https://$urlPrefix/repos/${ownerWithRepo}/pulls") {
                headers {
                    append(HttpHeaders.Authorization, "Bearer $githubKey")
                }
                parameter("state" , "closed")
                parameter("sort", "updated")
                parameter("direction", "desc")
                parameter("per_page" , "100")
                parameter("page" , page)
            }.body()

    }

    fun getPulls() {
        _uiState.update {
            it.copy(isLoading = true)
        }
        viewModelScope.launch {
            val pullRequests = loadPullRequests(uiState.value.currentPage)

            _uiState.update {
                it.copy(isNextShown = pullRequests.size == 100, currentPage = uiState.value.currentPage + 1)
            }

            val requests = pullRequests.map {
                async {
                    loadReviews(it.number, it.user)
                }
            }
            val reviews = requests.awaitAll().flatten()
            totalReviews.addAll(reviews)

            val data = totalReviews.groupBy { it.user }.map { (user, work) ->
                UserContribution(user.login,
                    approved =  work.filter { it.state == "APPROVED" }.size,
                    commented = work.filter { it.state == "COMMENTED" }.size ,
                    receivedComments = work.filter { it.state == "REVIEW" }.size,
                    created = work.filter { it.state == "CREATED" }.size
                )
            }.sortedByDescending { sort(_uiState.value.sortTypes, it) }

            _uiState.update {
                val totalCount = it.pulls + pullRequests.size
                it.copy(data = data, isLoading = false, pulls = totalCount)
            }
        }
    }

    private suspend fun loadReviews(number: Int, user: User): List<Review> {
        val urlPrefix = prepareurl(isEnterprise, enterprise)
        val allReviews = httpClient
            .get("https://$urlPrefix/repos/${ownerWithRepo}/pulls/${number}/reviews") {
                headers {
                    append(HttpHeaders.Authorization, "Bearer $githubKey")
                }
            }
            .body<List<Review>>()
        val nonUserReview = allReviews.filter { it.user.login != user.login }
        return nonUserReview + List(allReviews.size - nonUserReview.size){
            Review(user , "REVIEW")
        } + Review(user, "CREATED")
    }

    override fun onCleared() {
        httpClient.close()
    }

    fun onChangeFilter(type: SortTypes) {
        _uiState.update { state ->
            state.copy(sortTypes = type, data = state.data.sortedByDescending { sort(type, it) })
        }
    }

    private fun sort(sortTypes: SortTypes, it: UserContribution) = when (sortTypes.name) {
        SortTypes.APPROVALS.name -> it.approved
        SortTypes.COMMENTS.name -> it.commented
        else -> it.receivedComments
    }
}